@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Noto+Kufi+Arabic:wght@500&family=Roboto:wght@400;500;700&display=swap');

*,
*::after,
*::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: inherit;
}

body {
  background-image: radial-gradient(73% 147%, #eadfdf 59%, #ece2df 100%),
    radial-gradient(
      91% 146%,
      rgba(255, 255, 255, 0.5) 47%,
      rgba(0, 0, 0, 0.5) 100%
    );
  background-blend-mode: screen;
  font-family: 'Noto Kufi Arabic', sans-serif;
  user-select: none;
  color: #333;
}

nav {
  background-image: url('./assets/background-img.jpg');
  background-repeat: no-repeat;
  background-position: 50% 10%;
  background-color: transparent;
  background-size: cover;
  height: 500px;
}
nav .heading {
  box-shadow: rgba(4, 4, 4, 0.734) 10px 50px 500px 100px;
  background-color: rgba(4, 4, 4, 0.462);
}
.menu {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 10px -40px inset,
    rgba(0, 0, 0, 0.3) 0px 30px 10px -50px inset,
    rgba(41, 10, 64, 0.078) 0px -2px 6px 0px inset;
  /* border-top: 2px solid rgba(30, 0, 51, 0.437); */
}
.menu li {
  padding: 10px;
  list-style: none;
  transition: all 50ms;
}
.menu li:hover {
  color: #be2328;
  border-bottom: 2px solid #be2328;
  background-color: #33333305;
  box-shadow: rgba(4, 4, 4, 0.038) 0px 15px 10px 5px;
}
.menu a {
  text-decoration: none;
  color: inherit;
}
.card {
  box-shadow: rgba(62, 0, 81, 0.221) 0px 0px 40px 5px;
}
.manga-img-container {
  box-shadow: rgba(62, 0, 81, 0.315) 0px 0px 40px 5px;
}
.scrollable {
  height: 400px;
  width: 300px;
  padding: 16px 10px;
  overflow-y: auto;
  box-shadow: rgba(30, 0, 40, 0.132) 0px 0px 10px 1px;
}
.chapters-section a {
  border: solid 1px #3333335e;
  min-width: 155px;
  padding: 5px;
  border-radius: 10px;
  display: inline-block;
  box-shadow: rgba(22, 0, 26, 0.327) 0px 0px 10px -3px;
}
.chapters-section a:hover {
  transform: scale(1.05);
  transition: all 250ms;
  box-shadow: rgba(4, 4, 4, 0.038) 0px 15px 10px 5px;
  color: #000;
}
.newP {
  color: #be2328;
  text-shadow: 0px 3px 15px #ff2e3597;
}
.newP:hover {
  color: #ff2e35;
}
.blink_me {
  animation: blinker 1.7s linear infinite;
}
@keyframes blinker {
  50% {
    opacity: 0;
  }
}
footer {
  box-shadow: rgba(44, 0, 75, 0.225) 0px 0px 50px 0px;
}
.lineArrowUp{
  position: sticky;
  left: 80%;
  bottom: 5%;
}
.lineArrowUp:hover {
  box-shadow: rgba(59, 0, 93, 0.3) 0 1px 3px 0, rgba(48, 0, 103, 0.15) 0 4px 8px 3px;
  background: #f4f2f4c0
}
/*__________________________ select box styling _______________________________ */
div.select-container {
  border: 1px solid #7777776a;
  border-radius: 0.25em;
  padding: 0.25em 0.5em;
  font-size: 1rem;
  cursor: pointer;
  line-height: 1.1;
  background-color: #fff;
  background-image: linear-gradient(to top, #f9f9f9, #fff 33%);
}
select {
  appearance: none;
  background-color: transparent;
  border: none;
  padding: 0 1em 0 0;
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  cursor: inherit;
  line-height: inherit;
  outline: none;
}
/* _______________________ buttons styling _________________________________ */
button {
  align-items: center;
  appearance: none;
  background-color: #fff;
  border-radius: 24px;
  border-style: none;
  box-shadow: rgba(0, 0, 0, 0.137) 0 3px 3px -1px,
    rgba(0, 0, 0, 0.093) 0 6px 10px 0, rgba(0, 0, 0, 0.06) 0 1px 14px 0;
  box-sizing: border-box;
  color: #3c4043;
  cursor: pointer;
  display: inline-flex;
  fill: currentcolor;
  font-size: 14px;
  font-weight: bold;
  height: 48px;
  justify-content: center;
  letter-spacing: 0.25px;
  line-height: normal;
  overflow: visible;
  padding: 2px 24px;
  position: relative;
  text-align: center;
  text-transform: none;
  transition: box-shadow 280ms cubic-bezier(0.4, 0, 0.2, 1),
    opacity 15ms linear 30ms, transform 270ms cubic-bezier(0, 0, 0.2, 1) 0ms;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  will-change: transform, opacity;
  z-index: 0;
}
@media only screen and (max-width: 600px) {
  button {
    padding: 0 12px;
    height: 38px;
  }
}
button:hover {
  background: rgba(30, 0, 40, 0.05);
}

button:active {
  box-shadow: 0 4px 4px 0 rgb(60 64 67 / 30%),
    0 8px 12px 6px rgb(60 64 67 / 15%);
  outline: none;
}

button:focus {
  outline: none;
  border: 2px solid #7f00c46c;
}

button:not(:disabled) {
  box-shadow: rgba(59, 0, 93, 0.3) 0 1px 3px 0,
    rgba(48, 0, 103, 0.15) 0 4px 8px 3px;
}

button:not(:disabled):hover {
  box-shadow: rgba(60, 64, 67, 0.3) 0 2px 3px 0,
    rgba(60, 64, 67, 0.15) 0 6px 10px 4px;
}

button:not(:disabled):focus {
  box-shadow: rgba(60, 64, 67, 0.3) 0 1px 3px 0,
    rgba(60, 64, 67, 0.15) 0 4px 8px 3px;
}

button:not(:disabled):active {
  box-shadow: rgba(60, 64, 67, 0.3) 0 4px 4px 0,
    rgba(60, 64, 67, 0.15) 0 8px 12px 6px;
}

button:disabled {
  box-shadow: rgba(60, 64, 67, 0.3) 0 1px 3px 0,
    rgba(60, 64, 67, 0.15) 0 4px 8px 3px;
}







/* ___________________ dark mode customizing ___________________ */
.darkmode-layer{
  box-shadow:inset;
}
.darkmode-toggle {
  z-index: 1;
  opacity: 0.9;
}
.darkmode--activated button.lineArrowUp:hover {
  box-shadow: rgba(89, 0, 103, 0.542) 0 1px 5px 0, rgba(48, 0, 103, 0.542) 0 4px 5px 6px;
  background-color: #000000
}
.darkmode--activated p,
.darkmode--activated h3,
.darkmode--activated a {
  /* color: #000; */
  color: #969696;
}
.darkmode--activated nav {
  filter: saturate(130%);
  box-shadow: rgba(100, 0, 193, 0.751) 1px -55px 50px 1px;
  /* z-index: -10; */
}
.darkmode--activated nav div{
  background-color: #00000044;
  box-shadow: rgba(0, 0, 0, 0.366) 0px 0px 40px 500px;
}
.darkmode--activated nav h1,
.darkmode--activated nav p{
  background-color: #00000000 !important;
  filter: saturate(200%);
  filter: contrast(150%);
}
.darkmode--activated .menu a {
  filter: saturate(200%);
  filter: contrast(150%);
}
.darkmode--activated .menu a:Hover {
  filter: saturate(200%);
  filter: contrast(150%);
}
.darkmode--activated .card img {
  filter: saturate(120%);
  box-shadow: rgba(195, 0, 255, 0.058) 0px 0px 40px 5px;
}
.darkmode--activated .card {
  filter: saturate(120%);
  box-shadow: rgba(195, 0, 255, 0.221) 0px 0px 40px 5px;
}
.darkmode--activated html,
.darkmode--activated html[class],
.darkmode--activated html[id],
.darkmode--activated body,
.darkmode--activated body[class],
.darkmode--activated body[id],
.darkmode--activated input {
  background: #0c0c0c !important;
}
.darkmode--activated div:last-child,
.darkmode--activated :not(span),
.darkmode--activated :not(a) {
  border-color: #4af !important;
  background-color: #0c0c0c;
}
.darkmode--activated a,
.darkmode--activated a[class] {
  color: #9cf !important;
}
.darkmode--activated a:hover {
  color: #39f !important;
}
.darkmode--activated li {
  color: #8bf !important;
}
.darkmode--activated tr {
  color: #f00 !important;
}
.darkmode--activated cite {
  color: #268 !important;
}
.darkmode--activated input {
  color: #5bf !important;
}
.darkmode--activated em {
  color: #def !important;
}
.darkmode--activated ul {
  color: #f00 !important;
}
.darkmode--activated nobr {
  color: #f00 !important;
}
.darkmode--activated div {
  color: #bdf !important;
}
.darkmode--activated strong {
  color: #9cc !important;
}
.darkmode--activated s {
  color: #f00 !important;
}
.darkmode--activated h1 {
  color: #abc !important;
}
.darkmode--activated h2 {
  color: #def !important;
}
.darkmode--activated h3 {
  color: #cff !important;
}
.darkmode--activated * {
  color: #7cf !important;
}
.darkmode--activated b {
  color: #7af !important;
}
.darkmode--activated a,
.darkmode--activated span,
.darkmode--activated p,
.darkmode--activated div,
.darkmode--activated textarea,
.darkmode--activated i,
.darkmode--activated td {
  color: #ace !important;
  text-shadow: -1px -1px #0c0c0c, 1px -1px #0c0c0c, -1px 1px #0c0c0c,
    1px 1px #0c0c0c !important;
}
.darkmode--activated a > span.Sprite16 {
  filter: invert(100%);
  -webkit-filter: invert(100%);
}
.darkmode--activated button:hover {
  box-shadow: rgba(63, 0, 95, 0.562) 0px 1px 30px 3px;
}
.darkmode--activated button {
  border: 2px solid #52007e6c;
}